























import { Vue, Component, Emit, Prop } from 'vue-property-decorator';
import Button from './button.vue';

@Component
export default class Model extends Vue {
  @Prop(String) private title!: string;

  private mounted() {
    document.body.appendChild(this.$el);
  }

  @Emit('on-close')
  private close() {}

  @Emit('on-confirm')
  private confirm() {}

  @Emit('on-cancel')
  private cancel() {}
}
